* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	background-color: #068d3d;
	color: rgb(129, 129, 129);
	font-family: 'Titillium Web';
	font-size: 20px;
	font-weight: 300;
	text-rendering: optimizeLegibility;
	scroll-behavior: smooth;
}

header {
	color: rgb(196, 108, 9);
	background-image: linear-gradient(
			rgba(46, 46, 46, 0.616),
			rgba(46, 46, 46, 0.616)
		),
		url('../img/20200523_095811.jpg');
	background-size: cover;
	background-position: unset;
	background-attachment: fixed;
	height: 100vh;
	text-align: center;
}

header h1 {
	font-size: 70px;
	margin-top: 15%;
	text-align: center;
	text-shadow: 4px 4px rgba(4, 49, 0, 0.719);
}

h2 {
	font-size: 40px;
}

h3 {
	font-size: 30px;
}

/* ****************** */
/* ****** NAVI *****  */
/* ****************** */

.main-nav {
	display: inline-block;
	text-transform: uppercase;
	font-weight: 400;
}

.nav-links {
	margin-top: 67px;
	justify-content: space-between;
}

.li-blog,
.li-about {
	margin-right: 200px;
}

.li-photos,
.li-join {
	margin-left: 200px;
}

.nav-center {
	position: absolute;
	left: 50%;
	top: 10%;
	transform: translate(-50%, -50%);
}

.nav-center img {
	width: 75px;
	border-radius: 50%;
}

.main-nav a:link,
.main-nav a:visited {
	color: rgb(253, 138, 6);
	text-decoration: none;
}

.main-nav a:hover,
.main-nav a:active {
	color: #068d3d;
	border-bottom: 2px solid;
	transition: bottom-border 0.8s, color 0.3s;
}

/* ****************** */
/* ***** MOBILE ****  */
/* ****************** */

.mobile-nav {
	display: none;
}
.mobile-links {
	display: none;
}

.mobile-nav a:link,
.mobile-nav a:visited {
	color: rgb(253, 138, 6);
	text-decoration: none;
}

.mobile-nav a:hover,
.mobile-nav a:active {
	color: #068d3d;
	border-bottom: 2px solid;
	transition: bottom-border 0.8s, color 0.3s;
}

/* ****************** */
/* ****** BODY *****  */
/* ****************** */

.container {
	display: grid;
	height: 250vh;
	grid-template-columns: 1fr 2fr 1fr;
	grid-template-rows: 2fr 1fr 1fr 1fr 1fr;
	grid-template-areas:
		'about about about-img'
		'post1-img post1 post1'
		'post2 post2 post2-img'
		'post3-img post3 post3'
		'join join join1-img';
	grid-gap: 10px;
	color: #ddd;
	padding: 100px 150px 0 150px;
	max-width: 100em;
}

/* ABOUT */

.about {
	grid-area: about;
}

.about-img {
	grid-area: about-img;
}
.about-img img {
	margin-left: 30px;
	border-radius: 5%;
	background-image: url(/img/20200227_124348.jpg);
}

/* POSTS */

.post1 {
	grid-area: post1;
}

.post2 {
	grid-area: post2;
}

.post3 {
	grid-area: post3;
}

.post1-img {
	margin-right: 30px;
	grid-area: post1-img;
}

.post1-img img {
	margin-left: 20px;
	border-radius: 50%;
}

.post2-img {
	grid-area: post2-img;
}

.post2-img img {
	margin-left: 5px;
	border-radius: 25%;
}

.post3-img {
	grid-area: post3-img;
}

.post3-img img {
	margin-left: 80px;
	border-radius: 10%;
}

/* FORM */

.join {
	background-color: beige;
	color: #333;
	padding-left: 50px;
	margin-bottom: 10px;
	border: 2px solid #333;
	border-radius: 4px 4px;
	grid-area: join;
}

.join1-img {
	grid-area: join1-img;
}

.join1-img img {
	border-radius: 10px;
}

.join input {
	margin-top: 30px;
	font-size: 20px;
}

.btn {
	padding: 3px;
	margin-top: 20px;
	float: left;
	background-color: chocolate;
	border: none;
	border-radius: 10px;
	width: 100px;
	height: 40px;
}

.btn:hover {
	background-color: rgb(253, 138, 6);
	color: white;
	border: 2px solid rgb(0, 132, 255);
	cursor: pointer;
}

/* ****************** */
/* **** FOOTER *****  */
/* ****************** */

footer {
	display: flex;
	justify-content: center;
	flex-direction: row;
	justify-content: center;
	position: absolute;
	width: 100%;
	background: #333;
	padding: 50px;
	font-size: 80%;
}

.foot a {
	color: rgb(129, 129, 129);
}

.footer-nav {
	float: left;
	margin-left: -15rem;
}

.footer-nav a {
	padding: 20px;
	font-size: 125%;
	color: rgb(129, 129, 129);
}

.social-icons {
	float: right;
	margin-right: -12rem;
	font-size: 200%;
}

.social-icons a {
	padding: 20px;

	transition: color 0.2s;
}

.footer-nav a:visited,
.footer-nav a:link,
.ion-social-instagram-outline a:link,
.ion-social-instagram-outline a:visited,
.ion-social-youtube-outline a:link,
.ion-social-youtube-outline a:visited {
	transition: color 0.5s;
}

.footer-nav a:hover {
	color: #068d3d;
}

.ion-social-instagram:hover {
	color: #e95950;
}

.ion-social-youtube:hover {
	color: #e62117;
}
.ion-social-facebook:hover {
	color: #3b5999;
}

/* ****************** */
/* **** QUERIES ****  */
/* ****************** */

@media only screen and (max-width: 1325px) {
	header h1 {
		font-size: 60px;
	}
	.container {
		padding: 100px 150px 0 175px;
	}

	.li-blog,
	.li-about {
		margin-right: 150px;
	}

	.li-photos,
	.li-join {
		margin-left: 150px;
	}
}

@media only screen and (max-width: 1170px) {
	header h1 {
		margin-top: 25%;
	}
	.container {
		padding: 100px 100px 0 100px;
		font-size: 18px;
	}
}

@media only screen and (max-width: 1101px) {
	header h1 {
		font-size: 50px;
	}
	.li-blog,
	.li-about {
		margin-right: 100px;
	}

	.li-photos,
	.li-join {
		margin-left: 100px;
	}
}

@media only screen and (max-width: 1000px) {
	.container {
		padding: 70px 75px 0 75px;
	}
}

@media only screen and (max-width: 925px) {
	header h1 {
		font-size: 45px;
	}
	.container {
		margin: 0;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr;
		grid-gap: 50px;
		grid-template-areas:
			'about about'
			'about-img post1'
			'post2 post2'
			'post1-img post2-img'
			'post3 post3'
			'post3-img '
			'join join1-img';
		padding: 75px 20px 0 20px;
	}

	.join {
		padding-top: 40px;
		padding-bottom: 60px;
		margin-left: 30px;
	}

	.container img {
		height: 160px;
		width: auto;
	}

	.join1-img img {
		height: 300px;
		width: auto;
	}

	footer {
		margin-top: -10px;
	}
}

@media only screen and (max-width: 855px) {
	h2,
	h3 {
		font-size: 18px;
	}

	.container img {
		height: 120px;
		width: auto;
	}

	.join1-img img {
		height: 250px;
		width: auto;
	}

	.footer-nav {
		float: left;
		margin-left: -10rem;
	}

	.footer-nav a {
		padding: 20px;
		font-size: 125%;
		color: rgb(129, 129, 129);
	}

	.social-icons {
		float: right;
		margin-right: -8rem;
		font-size: 200%;
	}
}

@media only screen and (max-width: 700px) {
	header {
		background-image: linear-gradient(
				rgba(46, 46, 46, 0.616),
				rgba(46, 46, 46, 0.616)
			),
			url('../img/20200523_100207.jpg');
	}

	header h1 {
		margin: 0;
		padding-top: 450px;
	}

	.hidden {
		opacity: 0;
	}

	.main-nav {
		display: none;
	}

	.mobile-nav {
		display: block;
		position: fixed;
		background-color: rgba(51, 51, 51, 0.795);
		text-decoration: none;
		height: 80px;
		width: 100%;
	}

	.mobile-links {
		margin-top: 80px;
		margin-left: 80px;
		width: 100px;
	}

	.mobile-links a {
		display: block;
		background-color: rgb(75, 74, 74);
		border: 2px solid rgb(253, 138, 6);
		border-radius: 8px 10px;
		padding: 0;
		float: left;
		width: 100px;
		margin-left: 25px;
	}

	.mobile-nav i {
		font-size: 50px;
		float: left;
	}

	.ion-ios-paw {
		margin-left: 20px;
	}
	.ion-ios-paw-outline {
		margin-left: 5px;
	}

	.logo img {
		width: 65px;
		border-radius: 50%;
		float: right;
		margin-right: 20px;
		margin-top: 6px;
	}

	.container {
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
		grid-template-areas:
			'about'
			'about-img'
			'post1'
			'post1-img'
			'post2'
			'post2-img'
			'post3'
			'post3-img'
			'join';
		grid-gap: 1px;
		padding: 75px 50px 0 50px;
		align-items: center;
		justify-items: center;
		font-size: 15px;
	}

	.post3-img img {
		margin-left: 0;
	}

	form {
		margin-left: 0;
	}

	.join {
		padding-right: 40px;
	}

	.join1-img {
		display: none;
	}

	.footer-nav {
		float: left;
		margin-left: -8rem;
	}

	.social-icons {
		float: right;
		margin-right: -5.8rem;
		font-size: 200%;
	}
}

@media only screen and (max-width: 560px) {
	.mobile-links {
		margin-left: 5px;
	}
	.container {
		padding: 75px 20px 0 20px;
	}

	.footer-nav {
		float: left;
		margin-left: -2.3rem;
	}

	.social-icons {
		float: right;
		margin-right: -0.5rem;
		font-size: 200%;
	}
}

@media only screen and (max-width: 499px) {
	.mobile-links {
		margin-left: 0;
	}
}
@media only screen and (max-width: 460px) {
	header h1 {
		margin-top: 0;
	}

	.join {
		padding-right: 20px;
		padding-left: 25px;
		margin-left: 10px;
	}

	.footer-nav {
		float: left;
		margin-left: 2rem;
		font-size: 75%;
	}

	.social-icons {
		float: right;
		padding-top: 20px;
		margin-right: 5rem;
		font-size: 100%;
	}
}

@media only screen and (max-width: 394px) {
