		padding: 75px 5px 0 5px;
	}

	.join {
		padding-right: 20px;
		padding-left: 25px;
		margin-left: 0;
	}

	.social-icons {
		float: right;
		padding-top: 20px;
		margin-right: 3.7rem;
		font-size: 100%;
	}
}
